//colors
$color_0: rgb(0, 186, 195);
$color_1: rgb(1, 160, 168);

//fonts
$font_0: Raleway;
$font_1: sans-serif;

//urls
$url_0: url(../img/bridge_bg_sm.jpg);
$url_1: url(../img/bridge_logo.png);

body {
    background-repeat: no-repeat;
    background-size: cover;
    font-family: $font_0, $font_1;
    min-width: 320px;
    position: relative;
    line-height: 1.42857;
    min-width: 320px;
    -webkit-font-smoothing: antialised; }

.loader {
    background: none repeat scroll 0 0 #ffffff;
    bottom: 0;
    height: 100%;
    left: 0;
    position: fixed;
    right: 0;
    top: 0;
    width: 100%;
    z-index: 9999;
    background-image: url("../img/loader2.gif");
    background-repeat: no-repeat;
    margin: 0 auto;
    background-position: center center; }

.checkboxes_ships {
    margin-top: 10px;
    text-align: center;
    font-size: 20px; }

.hint1 {
    margin: 0 auto;
    padding: 5px;
    text-align: center; }
.hint2 {
    margin: 0 auto;
    padding: 5px;
    text-align: center; }

.chb {
    padding: 10px;
    text-align: center;
