.button_holder {
    padding-top: 20px;
    margin-top: 20px;
    text-align: center;
    width: 100%; }

#button12 {
    //If you use compass, instead of the line below you could use + border-radius($radius, $vertical-radius)
    text-transform: uppercase;
    text-decoration: none;
    color: white;
    border-radius: 8px;
    margin: 0 auto;
    font-size: 16px;
    font-weight: normal;
    margin: 15px;
    background-color: $color_0;
    letter-spacing: 1px;
    padding: 10px 20px;
    border: 1px solid #02868d;
    //If you use compass, instead of the line below you could use + transition($transition-1, $transition-2, $transition-3, $transition-4, $transition-5, $transition-6, $transition-7, $transition-8, $transition-9, $transition-10)
    transition: background-color 0.3s ease;
    box-shadow: none !important;
    //border: none !important
    //text-shadow: none !important
    //color: transparent !important
    &:focus {
        outline: 0 !important; }
    &:hover {
        background-color: $color_1; }
    &:active {
        fill:#1a82b8 {}
        background-color:#1a82b8 {} } }

#header1 {
    padding-bottom: 10px;
    font-weight: 600;
    font-style: normal;
    text-align: center;
    font-family: $font_0, $font_1; }

#header2 {
    padding-bottom: 10px;
    font-weight: 600;
    font-style: bold;
    text-align: center;
    font-family: $font_0, $font_1; }

